//~~~~~~~~~~~~~~~~~~~~~~~~~~//
//~~~~~~* TYPOGRAPHY *~~~~~~//+
//~~~~~~~~~~~~~~~~~~~~~~~~~~//


//~~ Fonts: Proxima Nova ~~/
@include font-face(ProximaNova, "../fonts/Proxima Nova/ProximaNova-Regular", normal, null, ttf);


//~~ Fonts: Roboto ~~/
@include font-face(Roboto, "../fonts/Roboto/Roboto-Regular", normal, null, ttf);


//~~ Headings ~~/
// .primary-heading
$primary-heading__font-size: $base__font-size * 2 !default;
$primary-heading__font-weight: $font-weight__lighter !default;
$primary-heading__text-transform: uppercase !default;
// ::after
$primary-heading__after__width: #{(150 / 16)}em !default;
$primary-heading__after__height: #{(3 / 16)}em !default;
$primary-heading__after__background: $primary-color !default;
$primary-heading__after__font-size: $primary-heading__font-size / 4 !default;
$primary-heading__after__border-radius: 20% !default;
$primary-heading__after__margin: #{(5 / 16)}em auto 0 !default;
// .secondary-heading .tertiary-heading .quaternary-heading
$secondary-heading__font-size: $primary-heading__font-size - 0.5 !default;
$tertiary-heading__font-size: $secondary-heading__font-size - 0.25 !default;
$headings__font-weight: $base__font-weight !default;
$headings__padding-bottom: #{(4 / 16)}em !default;
$headings__border-bottom: 1px solid $primary-color !default;
$quaternary-heading__font-size: $tertiary-heading__font-size - 0.1 !default;


