//~~ Text ~~//
//__normal
$text-normal__font-size: $base__font-size !default;
//__small
$text-small__font-size: $text-normal__font-size - 0.15 !default;
//__medium
$text-medium__font-size: $text-normal__font-size + 0.15 !default;
//__large
$text-large__font-size: $text-medium__font-size + 0.15 !default;
//-muted
$text-muted__color: $secondary-text-color !default;
// strong
$strong__font-weight: $font-weight__bold !default;


